.galleryWrap{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    align-items: center;
    justify-content: center;
    max-width: 1300px;
    margin: 0 auto;
}
.galleryWrap .single{
    width: 250px;
    cursor: pointer;
}
.galleryWrap .single img{
    max-width: 100%;
}
.galleryWrap .single img:hover{
    transform: scale(1.02);
}

.sliderWrap{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999;
    background-color: rgba(0, 0, 0, 0.8);
    display:flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.sliderWrap .btnClose, .sliderWrap .btnPrev, .sliderWrap .btnNext{
    position: fixed;
    cursor: pointer;
    opacity: 0.6;
    color: white;
    z-index: 9999;
}


.btnClose:hover, .btnPrev:hover, .btnNext:hover{
    opacity: 1;
}

.sliderWrap .btnClose{
    font-size: 50px;
    top:40px;
    right: 40px;
}

.sliderWrap .btnPrev{
    font-size: 50px;
    top:50%;
    transform: translateY(-50%);
    left: 40px;
}

.sliderWrap .btnNext{
    top:50%;
    font-size: 50px;
    transform: translateY(-50%);
    right: 40px;
}

.fullScreen{
    width: calc(100%-40px);
    height: calc(100%-40px);
    display: flex;
    align-items: center;
    justify-content: center;
}

.fullScreen img{
    max-width: 100%;
    max-height: 100%;
    pointer-events: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;

}